import React, { Component } from 'react';
import Highlight from 'react-highlight';
import LearnMore from './LearnMore';
import '../node_modules/highlight.js/styles/github-gist.css';
import './App.css';

function Step(props) {
  return (
    <div className={'step ' + (props.isCurrentStep ? 'current' : '')}>
      <div className="number">{props.number}</div>
      {props.children}
    </div>
  );
}

class App extends Component {
  state = {
    currentStep: 0,
  }

  render() {
    const { currentStep } = this.state;

    return (
      <div className="App">
        <header>
          <span className="icon">🚀</span>
          <h2>Getting Ready for the</h2>
          <h1>React Native Workshop</h1>
        </header>

        <div className="image">
          <img src="./static/comic.png" alt="Comic" />
        </div>

        <p>
          Please, take some time to read the following. It will help you being ready for the workshop.
        </p>
        <p>
          If you have any question, please let me know or Slack me! 🙂
        </p>

        <Step number={1} isCurrentStep={currentStep === 0}>
          <p>
            A quick introduction to React <strong>components</strong>, <strong>state</strong> and <strong>props</strong> <em>— skip it if you're fine with React.js</em>.
          </p>

          <div className="video">
            <iframe title="Tutorial" src="https://www.youtube.com/embed/ZkD4qW924jU?rel=0&amp;showinfo=0" frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe>
          </div>

          <ul>
            <li>
              <strong>A component first job is to render "HTML-like" code.</strong> It is defined using a class and requires at least a <code>render()</code> method.
            </li>
            <li>
              A component state holds only the information relevant to that component.<br />
              <Highlight className="javascript">
                {`class SpecialButton extends React.Component {
  state = { color: 'red' };

  render() {
    return (
      <div style={{ color: this.state.color }}>
        Hi
      </div>
    );
  }
}
`}
              </Highlight>
            </li>
            <li>
              You can pass extra information through properties, also called <em>props</em>.<br />
              <Highlight className="javascript">
                {`<div style={{ color: this.props.color }}>...</div>`}
              </Highlight>
              It is often used for configurating a predefined component.<br />
              <Highlight className="html">
                {`<Text color="red" size={16} ... />`}
              </Highlight>
            </li>
            <li>
              Components can be nested.<br />
              <Highlight className="html">
                {`<Comment>
  <AuthorName name="Thomas" />
  <Text>...</Text>
  <Date at={new Date(...)} />
</Comment>`}
              </Highlight>
            </li>
          </ul>

          <LearnMore>
            If you are curious about React, <a href="https://reactjs.org/tutorial/tutorial.html" target="_blank" rel="noopener noreferrer">the official tutorial</a> can teach you more.
          </LearnMore>
        </Step>

        <Step number={2}>
          <p>
            Basically, React Native is a superset of components and APIs, ready-to-use to create native mobile experiences.
          </p>

          <p>
            Although the syntax remains similar to HTML, <strong>we need to learn a few component names</strong> to build a UI:<br />
            <Highlight className="html">
              {`<div style={{ display: 'flex' }}>
  <p>
    Hello World
  </p>
</div>`}
            </Highlight>
            Will become in React Native:<br />
            <Highlight className="html">
              {`<View style={{ flex: 1 }}>
  <Text>
    Hello World
  </Text>
</View>`}
            </Highlight>
          </p>

          <ul>
            <li><em>View</em>, represents a container, a block (= UIView, View)</li>
            <li><em>Text</em>, ...for text 🤨</li>
            <li><em>Image</em></li>
            <li><em>TextInput</em>, for handling input (password, ...)</li>
            <li>Use the <em>style</em> attribute to customize the way it looks</li>
          </ul>

          <p>
            Here's one example of a simple UI screen, <a href="https://facebook.github.io/react-native/docs/handling-text-input" target="_blank" rel="noopener noreferrer">from the documentation</a>:

            <Highlight>
              {`class PizzaTranslator extends React.Component {
  state = {
    text: '',
  };

  onChangeText = (text) => {
    this.setState({ text: text });
    // or can be written as: this.setState({ text }); 
    // as the key name equals the variable name
  };

  render() {
    return (
      <View style={{ padding: 10 }}>
        <TextInput
          style={{ height: 40 }}
          placeholder="Type here to translate!"
          onChangeText={this.onChangeText}
        />

        <Text style={{ padding: 10, fontSize: 42 }}>
          {this.state.text
            .split(' ')
            .map((word) => word && '🍕')
            .join(' ')
          }
        </Text>
      </View>
    );
  }
}`}
            </Highlight>
          </p>

          <ul>
            <li><strong>PizzaTranslator is a React component.</strong> Its state holds the current text input value (<em>this.state.text</em>)</li>
            <li>The <em>TextInput</em> has a placeholder (i.e. message to show before entering text), and calls an instance method that updates the component state whenever the input inner text has changed</li>
            <li>What appears inside the <em>Text</em> component, is the current state text value, that turns every single word into a pizza emoji (<em>transformation</em> done using <a href="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/String/split" target="_blank" rel="noopener noreferrer">.split</a>, <a href="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/map" target="_blank" rel="noopener noreferrer">.map</a>, and <a href="https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/join" target="_blank" rel="noopener noreferrer">.join</a>)</li>
          </ul>

          <LearnMore>
            Layout is based on FlexBox. Here's <a href="http://flexboxfroggy.com/#zh-tw" target="_blank" rel="noopener noreferrer">a funny online game to learn about it</a> (in Chinese), and here's <a href="https://facebook.github.io/react-native/docs/flexbox" target="_blank" rel="noopener noreferrer">the documentation to read about its implementation</a> in React Native.
          </LearnMore>
        </Step>

        {/* Implementer ça */}
        <Step number={3} skippable>
          <p>
            To use React Native, you will need Node to be installed.<br />
            If not, use <a href="https://nodejs.org/en/download/" target="_blank" rel="noopener noreferrer">the official installers</a> or Brew:
            <Highlight className="console">
              $ brew install node
            </Highlight>
            We now need to install a tool that allows us to quickly setup React Native projects, <em>create-react-native-app</em>. In your terminal, paste the following:
            <Highlight className="console">
              $ npm install -g create-react-native-app
            </Highlight>
            To make sure the whole setup works fine, run the following commands:
            <Highlight className="console">
              {`$ create-react-native-app Workshop # create a Workshop application
$ cd Workshop
$ npm start # run local tools to start creating our project`}
            </Highlight>
            At this point, you can even try experimenting by following the in-console instructions.
          </p>

          <LearnMore>
            If you have free time and are curious, <a href="https://facebook.github.io/react-native/docs/tutorial" target="_blank" rel="noopener noreferrer">there is a not too long official tutorial for React Native</a>. It could be a good start if you really want to feel comfortable during the workshop.
          </LearnMore>
        </Step>

        <Step number={4}>
          <p>
            Now you may wonder: <em>what are we going to do with that?</em>
          </p>
          <p>
            We'll be building a simplified Twitter-like application, allowing users to register, write tweets and see other users' profile. The application will highlight many aspects of the React Native APIs.
          </p>
          <p>
            The idea is to make a cross-platform application within 1.5 hours, for React Native beginners (let's try).
          </p>

          <div className="image">
            <img src="./static/screenshot.jpeg" alt="Screenshot" />
          </div>

          <p>
            <strong>I will share with you a Git project later this week</strong> with all the non-react native related parts already implemented. The objective of this workshop is to see what React Native has to offer, more than JavaScript itself.
          </p>
        </Step>

        <Step number={5}>
          <p>
            Alright, our next step now will be on Friday, at 2:30pm.
          </p>
          <p>
            I also wanted to thank you for having attended the first sharing session, a few weeks ago. I am so glad and happy to share something with all of you, really.
          </p>
          <p>
            See you on Friday! 🧙‍♂️
          </p>
        </Step>

        {/*

  A: I wonder how long it took him to come up with this website
  B: man, React Developers are *real* nerds

        */}
      </div>
    );
  }
}

export default App;



